<template>
  <div id="app">
    <div class="app-bg">
      <background />
    </div>
    <letter ref="letter" />
  </div>
</template>

<script>
import Letter from './components/letter';
import Background from './components/background';

export default {
  name: 'App',
  components: {
    Letter,
    Background
  },

  mounted() {
      this.done = false
      this.startAnimation()
    },
    methods: {
      async startAnimation() {
        await this.$refs.letter.write();
      }
    }
}
</script>

