<template>
  <pre v-html="text" class="letter">
  </pre>
</template>

<script>

import { writeMixin } from '@/common/mixin';
import letterText from './letter';

export default {
    name: 'Letter',

    mixins: [writeMixin],

    data () {
        return {
            letterText: letterText
        };
    },

    methods: {
        async write () {
            await this.writeTo(this.$el, letterText, 0, this.speed, false, 1);
        }
    }
}
</script>

<style scoped>
    .letter {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        padding: 20px;
        font-size: 16px;
        line-height: 35px;
        text-align: left;
        white-space: break-spaces;
        color: #fff;
    }
</style>